import BaseAdapter from './base-adapter';

export default class Beachfront extends BaseAdapter {
	constructor(options) {
		super(options);

		this.bidderName = 'beachfront';
		this.bidfloor = 0.01;
		this.debugAppId = '5768085';
		this.isDebugMode = options.isDebugMode;
	}

	prepareConfigForAdUnit(code, { appId }) {
		return {
			code,
			mediaTypes: {
				video: {
					playerSize: [640, 480]
				}
			},
			bids: [
				{
					bidder: this.bidderName,
					params: {
						bidfloor: this.bidfloor,
						appId: this.isDebugMode ? this.debugAppId : appId
					}
				}
			]
		};
	}
}
