import BaseAdapter from './base-adapter';

export default class AppnexusWebads extends BaseAdapter {
	constructor(options) {
		super(options);

		this.bidderName = 'appnexusWebAds';
		this.aliases = {
			appnexus: [this.bidderName]
		};
		// ToDo: PRIORITY LEVEL
	}

	prepareConfigForAdUnit(code, { placementId, sizes }) {
		return {
			code,
			mediaTypes: {
				banner: {
					sizes
				}
			},
			bids: [
				{
					bidder: this.bidderName,
					params: {
						placementId
					}
				}
			]
		};
	}
}
