import settings from './prebid-settings';
import BaseBidder from '../base-bidder';
import { getAdapters } from './adapters-registry';

export default class Prebid extends BaseBidder {
	constructor(...args) {
		super(...args);

		this.logGroup = 'prebid-bidder';
		this.name = 'prebid';
		this.adUnits = this.setupAdUnits();
	}

	setupAdUnits() {
		const adapters = getAdapters(this.bidderConfig);

		return adapters.map((adapter) => {
			if (adapter && adapter.enabled) {
				return adapter.prepareAdUnits();
			}
		});
	}

	removeAdUnits() {
		(window.prebid.adUnits || []).forEach((adUnit) => {
			window.prebid.removeAdUnit(adUnit.code);
		});
	}

	call(bidsBackHandler) {
		console.log('1');
		if (window.prebid) {
			// window.prebid._bidsReceived = [];
		}

		if (this.adUnits.length > 0) {
			window.prebid.que.push(() => {
				window.prebid.bidderSettings = settings.create();
			});

			window.prebid.que.push(() => {
				this.removeAdUnits();

				window.prebid.requestBids({
					adUnits: this.adUnits,
					bidsBackHandler
				});
			});
		}
	}

	isSlotSupported(slotName) {
		return this.adUnits.some((adUnit) => {
			return adUnit.code === slotName;
		});
	}

	getSlotParams(slotName) {
		let params;

		if (window.prebid && typeof window.prebid.getAdserverTargetingForAdUnitCode === 'function') {
			params = window.prebid.getAdserverTargetingForAdUnitCode(slotName) || {};
		}

		return params || {};
	}
}
