import BaseAdapter from './base-adapter';

export default class Rubicon extends BaseAdapter {
	constructor(options) {
		super(options);

		this.bidderName = 'rubicon';
		this.accountId = 7450;
	}

	prepareConfigForAdUnit(code, {
		siteId, zoneId, sizeId, position, targeting
	}) {
		return {
			code,
			mediaTypes: {
				video: {
					playerSize: [640, 480]
				}
			},
			bids: [
				{
					bidder: this.bidderName,
					params: {
						accountId: this.accountId,
						siteId,
						zoneId,
						name: code,
						position,
						inventory: targeting,
						video: {
							playerWidth: '640',
							playerHeight: '480',
							size_id: sizeId,
							language: (targeting.lang && targeting.lang[0]) || 'en'
						}
					}
				}
			]
		};
	}
}
